export const isObject = o => o instanceof Object && o.constructor === Object;

export const isString = o => o && typeof o === 'string';

export const getStringLength = (string, excludeWhitespaces) =>
  excludeWhitespaces ? string.replace(/\s/g, '').length : string.length;

export function trim(s) {
  return s.replace(/^\s+|\s+$/g, '');
}

export function ready(fn) {
  if (typeof fn !== 'function') return;

  if (document.readyState === 'complete') {
    return fn();
  }

  document.addEventListener('DOMContentLoaded', fn, false);
}

export function isEqualArrays(a1, a2) {
  let i = a1.length;

  // eslint-disable-next-line no-plusplus
  while (i--) {
    if (a1[i] !== a2[i]) {
      return false;
    }
  }

  return true;
}

export function convertFloat32ToInt16(buffer) {
  let l = buffer.length;
  const buf = new Int16Array(l / 3);

  // eslint-disable-next-line no-plusplus
  while (l--) {
    if (l % 3 === 0) {
      buf[l / 3] = buffer[l] * 0xffff;
    }
  }

  return buf.buffer;
}

export const pad = value => String(value).padStart(2, '0');

export function isIE() {
  const ua = window.navigator.userAgent;
  const msie = ua.indexOf('MSIE ');
  const trident = ua.indexOf('Trident/');

  return msie > 0 || trident > 0;
}
