/**
 * Module that can be used to include Google Analytics tracking code
 */
import loadGa from '../utils/loadGA';
import { isObject, trim } from '../utils/shims';

const internalGa = (...args) => {
  if (!window.ga) {
    return console.warn(
      'Initialize must be called first or GoogleAnalytics should be loaded manually'
    );
  }

  return window.ga(...args);
};

/**
 * ga:
 * Returns the original GA object.
 */
export function ga(...args) {
  if (args.length > 0) {
    internalGa(...args);
  }

  return window.ga;
}

/**
 * Wraper for more convenient way to perform ga commands
 * @param {Array} trackerNames - trackers to send the command to
 */
export function gaCommand(trackerNames, ...args) {
  const command = args[0];

  if (typeof command !== 'string') {
    console.warn('ga command must be a string');
    return;
  }

  if (!Array.isArray(trackerNames)) {
    internalGa(...args);
  } else {
    trackerNames.forEach(name => {
      internalGa(...[`${name}.${command}`].concat(args.slice(1)));
    });
  }
}

// private method for create ga
function _create(gaTrackingId, options = {}) {
  if (!gaTrackingId) {
    console.warn('gaTrackingID is required in create');
    return;
  }

  if (options && options.gaOptions) {
    internalGa('create', gaTrackingId, options.gaOptions);
  } else {
    internalGa('create', gaTrackingId, 'auto');
  }
}

export function create(configsOrTrackingId, options = {}) {
  if (Array.isArray(configsOrTrackingId)) {
    configsOrTrackingId.forEach(config => {
      if (!isObject(config)) {
        console.warn('All configs must be an object');
        return;
      }

      _create(config.trackingId, config);
    });
  } else {
    _create(configsOrTrackingId, options);
  }
}

/**
 * send:
 * Clone of the low level `ga.send` method
 * @param  {Object} fieldObject - field object for tracking different analytics
 * @param  {Array} trackerNames - trackers to send the command to
 * @param {Array} trackerNames - (optional) a list of extra trackers to run the command on
 */
export function send(fieldObject, trackerNames) {
  gaCommand(trackerNames, 'send', fieldObject);
}

/**
 * pageview:
 * Basic GA pageview tracking
 * @param  {String} path - the current page page e.g. '/about'
 * @param {Array} trackerNames - (optional) a list of extra trackers to run the command on
 * @param {String} title - (optional) the page title e. g. 'My Website'
 */
export function pageview(rawPath, trackerNames, title) {
  if (!rawPath) {
    console.warn('path is required in .pageview()');
    return;
  }

  const path = trim(rawPath);

  if (path === '') {
    console.warn('path cannot be an empty string in .pageview()');
    return;
  }

  const extraFields = {};

  if (title) {
    extraFields.title = title;
  }

  if (typeof ga === 'function') {
    gaCommand(trackerNames, 'send', {
      hitType: 'pageview',
      page: path,
      ...extraFields,
    });
  }
}

/**
 * ⭐ ADD YOUR TRACKERS IN THIS FUNCTION ⭐
 * -----------------------------------------
 * @param {Object} app - instance of VA application
 * @param {*} options - config options
 */
function addTrackers(app, options) {
  // create ga
  create(options.configsOrTrackingId);

  // add pageview
  pageview('/', options.trackerNames);

  // on app launcher button clicked
  app.on('launcherButtonClicked', (message, data) => {
    console.log(message, 'send info to ga', data);
  });

  // on opened
  app.on('opened', function() {
    console.log('opened');
  });

  // on closed
  app.on('closed', function() {
    console.log('closed');
  });

  // triggered when one of the
  // A/B test experiment variants is rendered
  app.on('AB.displayed', function(_, data) {
    console.log(data);
  });

  // triggered when the user clicks
  // on one of the variants of the A/B testing experiment
  app.on('AB.win', function(_, data) {
    console.log(data);
  });

  // ... other trackers
}

/**
 * Сalled to initialize google analytics.
 */
export function initialize(app, loadOptions) {
  // get google analytics config from VA config
  const [cfg] = app.config.files.filter(
    file => file.name === 'ga' && file.type === 'analytics'
  );

  if (!cfg) {
    console.error('ga analytics configuration file not found');
    return;
  }

  // If `ga` missing load manually.
  if (!window.ga) {
    loadGa(loadOptions);
  }

  // Trackers are added only if there are options for them.
  if (cfg.options) {
    addTrackers(app, cfg.options);
  }
}

window.__va__(app => initialize(app));
